import BaseLayoutSection from "./BaseLayoutSection";

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('BaseLayout', component.exports)
}
export default {
  name: "BaseLayout",
  components: {
    "base-layout-section": BaseLayoutSection
  },
  props: {
    layout: {
      type: String,
      default: "",
      validator: function(value) {
        return [
          "",
          "one-column",
          "two-columns-1-1",
          "two-columns-2-1",
          "two-columns-2-1-sticky",
          "three-columns",
          "annotated"
        ].includes(value);
      }
    }
  }
}
